import * as actionTypes from '../constants/userinfo.js'

export default function userinfo(state = {} ,action) {
    // const {type , payload} = action
    // console.log('reducers update')
    // console.log(type)
    // console.log(payload)
    switch (action.type) {
        case actionTypes.USERINFO_UPDATE:
            return action.payload
            break
        default:
            return state
            break
    }
}


