import React from 'react'
import {connect} from 'react-redux'
import HomeHead from '../../components/HomeHead/HomeHead.jsx'
import HomeCategory from '../../components/HomeCategory/HomeCategory.jsx'
import HomeList from './subpages/HomeList.jsx'
import Footer from '../../components/Footer/Footer.jsx'
import './style.less'

class Home extends React.Component {
    constructor(props,context) {
        super(props,context)
        this.state = {
            isShow: 'hide'
        }
    }

    render() {
        return (
            <div className="home">                
                <HomeHead address={this.props.userinfo.address}/>
                <HomeCategory />
                <HomeList />
                <div onClick={() => {this.goTop()}} className={`to-top ${this.state.isShow}`}></div>
                <Footer />
            </div>
        )
    }

    goTop() {
        let timer
        timer = setInterval(() => {
            let top = document.documentElement.scrollTop || document.body.scrollTop
            let speed = Math.ceil(top / 5)
            document.documentElement.scrollTop = document.body.scrollTop = (top - speed)
            if(0 == top) clearInterval(timer)
        },1)    
    }

    componentDidMount() {
        let timeoutId
        let Odiv = document.getElementById('home-head')
        let distance = () => {
            let high = Odiv.getBoundingClientRect().top
            if(high < -400) {
                this.setState({isShow: 'show'})
            } else {
                this.setState({isShow: 'hide'})
            }
        }
        distance()
        window.addEventListener('scroll',function() {
            if (timeoutId) {
                clearTimeout(timeoutId)
            }
            timeoutId = setTimeout(distance, 50)
        }.bind(this),false)
    }
}

//------------react-redux华丽的分割线-------------

function mapStateToProps(state) {
    return {
        userinfo: state.userinfo
    }
}

function mapDispatchToProps(dispatch) {
    return {}
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Home)


