import React from 'react'
import {Link} from 'react-router'
import './style.less'

function HomeHeader(props) {
    return (
        <div className="home-header">
            <Link to='/address'>{props.address}(假定位0.0)</Link>
        </div>
    )
}

export default HomeHeader


