import React from 'react'
import './style.less'
import {Link} from 'react-router'

class Footer extends React.Component {
    constructor(props,context) {
        super(props,context)
        this.state = {
            homeActive: true,
            orderAcive: false,
            mineActive: false
        }
    }

    render() {
        let home,order,mine = ''
        home = this.state.homeActive ? 'active' : 'normal'
        order = this.state.orderAcive ? 'active' : 'normal'
        mine = this.state.mineActive ? 'active' : 'normal'
        return (
            <div className="Footer">
                <Link to='/' className="a-home">
                    <div className={`nav-home-${home}`} onClick={() => {this.homeHandle.bind(this)}}></div>
                    <span>外卖</span>
                </Link>
                <Link to='/order' className="a-order">
                    <div className={`nav-order-${order}`} onClick={() => {this.orderHandle.bind(this)}}></div>
                    <span>订单</span>
                </Link>
                <Link to='/mine' className="a-mine">
                    <div className={`nav-mine-${mine}`} onClick={() => {this.mineHandle.bind(this)}}></div>
                    <span>我的</span>
                </Link>
            </div>
        )
    }

    homeHandle(sort) {
        if(true == this.state.homeActive) return 
        else this.setState({
            homeActive: true,
            orderAcive: false,
            mineActive: false
        })
    }

    orderHandle(sort) {
        if(true == this.state.orderActive) return 
        else this.setState({
            homeActive: false,
            orderAcive: true,
            mineActive: false
        })
    }

    mineHandle(sort) {
        if(true == this.state.mineActive) return 
        else this.setState({
            homeActive: false,
            orderAcive: false,
            mineActive: true
        })
    }
}

export default Footer


