import React from 'react'
import * as actionsFromOtherFile from '../actions/userinfo.js'
import {bindActionCreators} from 'redux'
import {USERINFO_ADDRESS} from '../localStoreKeys/localStoreKeys.js'
import {getCityName} from '../util/localStore.js'
import {connect} from 'react-redux'
import './style.less'

class App extends React.Component {
    constructor(props,context) {
        super(props,context)
        this.state = {
            isFetch: true
        }
    }

    render() {
        return (
            <div className="app">
                {this.state.isFetch ? <div className="app-load"></div> : <div>{this.props.children}</div> }
            </div>
        )
    }

    componentDidMount() {
        //获取城市信息
        let address = getCityName(USERINFO_ADDRESS)
        if(address == null) address = '深圳世界之窗'
        console.log('address:'+address)
        //更新仓库里得城市名
        this.props.actions.update({address:address})
        //更新状态
        //模拟加载时间
        let time = Math.floor(Math.random(1000)*1000)
        setTimeout(() => {this.setState({isFetch: false})}, time)
    }
}

//------------------------------
function mapStateToProps(state) {
    return {}
}

function mapDispatchToProps(dispatch) {
    return {
        actions: bindActionCreators(actionsFromOtherFile,dispatch)
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(App)




